body {
  margin: 0;
  background: #CCCCCC;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h2 {
  text-align: center;
}

.container {
  margin: 30px;
  padding: 30px;
  background: #EEEEEE;
}

.form-content {
  width: 300px;
  background: #FFFFFF;
}

.form-content div {
  padding: 5px;
}

.form-content label {
  width: 80px;
  float: left;
  text-align: right;
  padding: 5px;
}

.form-content input {
  height: 25px;
}

.form-content button {
  width: 120px;
  margin: 10px 0px 10px 110px;
}